<template>
  <div
    :class="[
      'filter-and-options',
      onlyOneOption && 'filter-and-options--only-one-option',
    ]"
  >
    <div
      v-if="!hideFilter"
      class="filter-and-options__item filter-and-options__filter noselect"
      @click="toggleFilterModal"
    >
      <i class="icon icon--sliders" />
      <span class="filter-and-options__filter-label"> {{ $t("filter") }}</span>
      <i class="filter-and-options__filter-arrow icon--arrow-down" />
    </div>

    <div
      v-if="!hideMoreOptions"
      class="filter-and-options__item filter-and-options__more-options noselect"
      @click="toggleShowMorePopup"
    >
      <i class="icon icon--grid" />
      <span class="filter-and-options__more-options-label">
        {{ $t("moreOptions") }}</span
      >
      <i class="filter-and-options__more-options-arrow icon--arrow-down" />
    </div>

    <MyProductAndSameAsLastBtns
      class="filter-and-options__item filter-and-options__my-product-same-as-last"
      :merchant="merchant"
      :storeId="storeId"
      sameAsLastFirst
    />
  </div>
</template>

<i18n>
{
  "is": {
    "filter": "Sía",
    "moreOptions": "Fleiri valkostir"
  },
  "en": {
    "filter": "Filter",
    "moreOptions": "More Options"
  }
}
</i18n>

<script>
import { mapActions } from "vuex";
import {
  MODAL_KEY_CATEGORIES_FILTER,
  MODAL_KEY_MORE_OPTIONS_POP_UP,
} from "@/store/modalModule";
import MyProductAndSameAsLastBtns from "@/components/MyProductAndSameAsLastBtns";

export default {
  name: "FilterAndOptions",
  components: {
    MyProductAndSameAsLastBtns,
  },
  props: {
    hideFilter: Boolean,
    hideMoreOptions: Boolean,
    merchant: Object,
    storeId: Number,
  },
  computed: {
    onlyOneOption() {
      if (!this.merchant) return;
      const showMyProduct = this.merchant.favorite_customer_products === 1;
      const showSameAsLast = !this.merchant.hide_same_as_last;
      return [showMyProduct, showSameAsLast].filter(Boolean).length === 1;
    },
  },
  methods: {
    ...mapActions(["toggleModal"]),
    toggleFilterModal() {
      this.toggleModal({
        modalKey: MODAL_KEY_CATEGORIES_FILTER,
        routerToReset: this.$route.fullPath,
      });
    },
    toggleShowMorePopup() {
      this.toggleModal({
        modalKey: MODAL_KEY_MORE_OPTIONS_POP_UP,
      });
    },
  },
};
</script>

<style lang="scss" scoped>
.filter-and-options {
  display: flex;
  flex-wrap: wrap;
  margin-bottom: 20px;
  margin-left: -15px;
  margin-right: -15px;
}

.filter-and-options__item {
  padding: 15px;
}

.filter-and-options__filter,
.filter-and-options__more-options {
  display: flex;
  align-items: center;
  cursor: pointer;
}
.filter-and-options__filter-label,
.filter-and-options__more-options-label {
  padding-left: 15px;
  padding-right: 15px;
}

.filter-and-options__filter-arrow,
.filter-and-options__more-options-arrow {
  font-size: 10px;
}
.filter-and-options__my-product-same-as-last {
  display: none;
}

.filter-and-options--only-one-option {
  .filter-and-options__more-options {
    display: none;
  }
  .filter-and-options__my-product-same-as-last {
    display: flex;
  }

  >>> .same-as-last .icon--time {
    font-size: 24px;
  }
}

>>> .same-as-last {
  .popup {
    left: 0;
    right: unset;
  }
}

@media screen and (min-width: 768px) {
  .filter-and-options {
    display: none;
  }
}
</style>
