<template>
  <!-- Provides a drop-down menu for repeating previous orders from the same merchant/user -->
  <div class="same-as-last">
    <base-button
      class="same-as-last__btn primary"
      v-click-outside="hidePopup"
      @click.native="togglePopup"
    >
      <span v-if="isPending" class="pending-text">{{ $t("fetching") }}...</span>
      <span class="same-as-last__title" v-else>
        <i class="icon icon--time" />
        <span>{{ $t("sameAsLast") }}</span>
        <i class="arrow-down" />
      </span>
    </base-button>
    <div class="popup" v-if="isPopupOpen">
      <div
        v-if="!pastOrders.length"
        :class="[(!isLoggedIn || !pastOrders.length) && 'popup__not-logged-in']"
      >
        <span v-if="isLoggedIn" class="no-orders">{{
          $t("noPastOrders")
        }}</span>
        <span v-else class="order" @click="$store.dispatch('toggleLogin')">{{
          $t("pleaseLogin")
        }}</span>
      </div>
      <div
        v-else
        v-for="{ id, date, amount } in pastOrders"
        :key="date"
        class="order"
        @click="placeReorder(id)"
      >
        <span class="order-date">{{ date }}</span>
        <span class="order-amount">{{ amount | formatISK }}</span>
      </div>
    </div>
  </div>
</template>

<i18n>
{
	"is": {
    "fetching": "Sæki pöntun",
		"sameAsLast": "Sama og síðast",
    "orderAdded": "Pöntun bætt í körfu!",
    "noPastOrders": "Þú hefur ekki pantað héðan áður. Nú er tækifærið!",
    "pleaseLogin": "Þú er ekki skráð/ur inn. Smelltu hér til að skrá þig inn og sjá eldri pantanir."
	},
	"en": {
    "fetching": "Fetching order",
		"sameAsLast": "Same as last time",
    "orderAdded": "Order added to cart!",
    "noPastOrders": "You have not ordered from here before. Now is your chance!",
    "pleaseLogin": "You are not logged in. Please click here to log in and see your past orders."
	}
}
</i18n>

<script>
import ClickOutside from "vue-click-outside";
import axios from "axios";
import { mapGetters, mapActions } from "vuex";
import BaseButton from "@/components/BaseButton";

export default {
  name: "SameAsLast",
  components: {
    BaseButton,
  },
  props: {
    storeId: Number,
  },
  data() {
    return {
      isPopupOpen: false,
      pastOrders: [],
      isPending: false,
    };
  },
  computed: {
    ...mapGetters(["getNettoIdByURL"]),
    isLoggedIn() {
      return this.$store.state.customer.is_logged_in;
    },
  },
  methods: {
    ...mapActions(["getStoreIdURLMap"]),
    togglePopup() {
      this.isPopupOpen = !this.isPopupOpen;
    },
    hidePopup() {
      this.isPopupOpen = false;
    },
    async placeReorder(orderID) {
      const storeUrl = this.$route.params.storeUrl;
      await this.getStoreIdURLMap();
      const storeId = this.getNettoIdByURL(storeUrl);
      this.isPending = true;
      const {
        data: { status, message },
      } = await axios.put("/api/rest/cart", {
        reorder: {
          super_product: storeId,
          corder_id: orderID,
        },
      });
      this.isPending = false;
      if (status === "success") {
        this.$store.dispatch("loadCart");
        this.$store.dispatch("notification", {
          type: "success",
          message: this.$t("orderAdded"),
        });
      } else {
        this.$store.dispatch("notification", {
          type: "error",
          message,
        });
      }
    },
    parseSuborders(suborders) {
      return Object.values(Object.values(suborders)[0].items);
    },
    async fetchPastOrders() {
      const { data } = await axios.get(
        `/api/rest/cart/past_orders/${this.storeId}`
      );
      this.pastOrders = Object.entries(data[0].past_orders).map(
        ([id, { order_on, sub_orders }]) => {
          const orderOnDate = new Date(order_on);
          // Locale is-IS gives incorrect date format and doesn't translate weekdays
          const date = orderOnDate.toLocaleDateString("en-GB");
          const amount = this.parseSuborders(sub_orders)
            .map(amount => +amount)
            .reduce((sum, amount) => sum + amount);
          return { id, date, amount };
        }
      );
    },
    handleTouchOutside() {
      if (!this.isPopupOpen) return;
      return this.hidePopup();
    },
  },
  directives: {
    ClickOutside,
  },
  watch: {
    isLoggedIn(isLoggedIn) {
      isLoggedIn && this.fetchPastOrders();
    },
    storeId() {
      this.isLoggedIn && this.fetchPastOrders();
    },
  },
  mounted() {
    this.isLoggedIn && this.fetchPastOrders();
    window.addEventListener("touchmove", this.handleTouchOutside);
  },
  beforeDestroy() {
    window.removeEventListener("touchmove", this.handleTouchOutside);
  },
};
</script>

