<template>
  <div
    v-if="!isLoading"
    :class="[
      'my-product-same-as-last',
      usingBtnStyle && 'my-product-same-as-last--btns',
      sameAsLastFirst && 'my-product-same-as-last--same-as-last-first',
    ]"
  >
    <div
      class="noselect my-products my-product-same-as-last__item"
      v-if="showMyProduct"
      @click="onMyProductClick"
    >
      <input
        class="my-products__input"
        type="checkbox"
        :checked="isMyProductsFiltered"
      />
      <span class="my-products__label">{{ $t("myProducts") }}</span>
    </div>
    <SameAsLast
      class="same-as-last--no-btn my-product-same-as-last__item"
      v-if="showSameAsLast"
      :storeId="storeId"
    />
  </div>
</template>

<script>
import SameAsLast from "@/components/SameAsLast";

export default {
  name: "MyProductAndSameAsLastBtns",
  components: {
    SameAsLast,
  },
  props: {
    usingBtnStyle: Boolean,
    onMyProductClick: {
      type: Function,
      default: () => {},
    },
    storeId: Number,
    sameAsLastFirst: Boolean,
    merchant: Object,
    isMyProductsFiltered: {
      type: Boolean,
      default: false,
    },
  },
  computed: {
    isLoading() {
      return !this.merchant;
    },
    showMyProduct() {
      return this.merchant.favorite_customer_products === 1;
    },
    showSameAsLast() {
      return !this.merchant.hide_same_as_last;
    },
  },
};
</script>

